import React, { Component } from "react";
import "./second.css";
import HomeHeader from "../../Reusable/Header/homeHeader";
import MainFooterPage from "../../Reusable/Footer/footer";
import artistpic from "../../Assets/images/kb.jpg";



export default class second extends Component {
  state = {
   
    loading: true,
   
  };


  render() {
    
  
      return (
        <div  id='second' >
          <HomeHeader
          
          />
          <div  className="container">

          <div className="homepageContent">
          <div className="artistDiv1" >
          <img className='ImageSize'   src={artistpic} />
          
          <div className='artistDetail'>
          <h1>Jb</h1>
          <p>Justin Drew Bieber (/ˈbiːbər/; born March 1, 1994) is a Canadian singer, songwriter, and actor.[4] Discovered at 13 years old by talent manager Scooter Braun after he had watched his YouTube cover song videos, Bieber was signed to RBMG Records in 2008. With Bieber's debut EP My World, released in late 2009, Bieber became the first artist to have seven songs from a debut record chart on the Billboard Hot 100.</p>
          </div>
          </div>
          </div>

         
        </div>
          
        <div className="container1">
        <div className="homepageContent">
          <h1>second Ganesh Music</h1>
          </div>
        </div>

        
         
              <MainFooterPage  />
           
        </div>
      );
    
  }
}
